.secondStep__dateWrap,
.secondStep__genderWrap {
  display: flex;
  border-right: none;
}
.secondStep__dateElementWrap {
  width: calc(100% / 3);
  position: relative;
  display: flex;
}

.secondStep__dateElement,
.secondStep__generElementWrap {
  width: calc(100% / 3);
  text-align: center;
  outline: none;
  padding: 10px;
  font-size: 19px;
  border: 1px solid #d0d0d0;
  color: #5c5b5c;
}

.secondStep__dateElement {
  width: 100%;
}

.secondStep__dateElement.error::placeholder {
  color: red;
}

.secondStep__dateError {
  color: red;
  font-size: 11px;
  position: absolute;
  bottom: -40%;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}

h2 {
  text-transform: uppercase;
  color: #5c5b5c;
  padding: 20px 0 10px;
  font-size: 17px;
  font-weight: normal;
  margin: 0;
}

.secondStep__title.error {
  color: red;
}

.secondStep__genderWrap {
  display: flex;
}

.secondStep__genderElementLabel {
  width: 100%;
  height: 100%;
  display: block;
  color: #5c5b5c;
  cursor: pointer;
}

.secondStep__generElementWrap.active {
  background: #4a90e2;
}

.secondStep__generElementWrap.active .secondStep__genderElementLabel {
  color: #fff;
}

.secondStep__aboutUsSelectWrap {
  position: relative;
}

.secondStep__aboutUsSelectWrap:before {
  content: "\203A";
  color: #4a90e2;
  position: absolute;
  left: 95%;
  top: 50%;
  transform: translateY(-50%) rotate(90deg);
  font-size: 25px;
  outline: none;
}

.secondStep__aboutUsSelect {
  width: 100%;
  font-size: 16px;
  color: #5c5b5c;
  padding: 10px;
  cursor: pointer;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}

.transition-enter {
  opacity: 0.01;
  transform: translate(0, -10px);
}
.transition-enter-active {
  opacity: 1;
  transform: translate(0, 0);
  transition: all 300ms ease-in;
}
.transition-exit {
  opacity: 1;
  transform: translate(0, 0);
}
.transition-exit-active {
  opacity: 0.01;
  transform: translate(0, 10px);
  transition: all 300ms ease-in;
}
